@import url(https://fonts.googleapis.com/css2?family=Courgette&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.wrapper-1374 {
  max-width: 1374px;
  margin: 0 auto;
}
.wrapper-1320 {
  max-width: 1320px;
  margin: 0 auto;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.pr-banner .swiper-button-prev:after, 
.pr-banner .swiper-button-next:after {
    font-size: 25px;
    font-weight: 700;
    color: #000;
}
.pr-banner .swiper-container {
    /* z-index: -1; */
}
.pr-banner .swiper-wrapper {
    z-index: -1;
}
.pr-banner .swiper-slide {
    z-index: -1;
}
.pr-banner .swiper-pagination {
    display: none !important;
}
.pink-section {
    position: relative;
    z-index: 1;
}
.header {
    width: 100%;
    height: 50px;
    background-color: gray;
    color: #fff;
    text-align: center;
    font-size: 30px;
    font-weight: 700;
    padding: 0px 0;
}
.bg-pink {
    height: 500px;
    margin-top: -130px;
    padding: 25px 25px 25px 25px;
    border-radius: 5px;
    background-image: linear-gradient(to bottom, #f8447e, #d92a62);
}

.Send-Gift-heading {
    -webkit-text-stroke: 1px #fff;
    font-family: 'Courgette', cursive;
    font-size: 36px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.25;
    letter-spacing: normal;
    text-align: left;
    color: #fff;
    white-space: nowrap;
}
.Send-Gift-subheading {
    font-family: 'Lato', sans-serif;
    font-size: 20px;
    font-weight: normal;
    font-stretch: normal;
    font-style: italic;
    line-height: 1.2;
    letter-spacing: normal;
    text-align: left;
    color: #fff;
}

.heading-and-img {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

}

.img-text {
    width: 125px;
    height: 125px;
    background: #ffffff;
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border: 1px solid #dbb868;
    position: relative;
    margin-top: -86px;
    margin-right: 50px;
}

.date {
    -webkit-text-stroke: 1px #f5437c;
    font-family: Courgette;
    font-size: 22px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.2;
    letter-spacing: 1.5px;
    text-align: center;
    color: #f5437c;
}
.day {
    -webkit-text-stroke: 1px #010000;
    font-family: Courgette;
    font-size: 18px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.2;
    letter-spacing: 1px;
    color: #000;
}

.img-text img {
    max-width: 80%;
    margin: 0 auto;
}
.img-text::before {
    position: absolute;
    content: '';
    width: 125px;
    height: 125px;
    left: -5px;
    top: -5px;
    border-radius: 50%;
    background: transparent;
    border: 1px solid #dbb868;
}
.gift-listing-inner {
    margin: 20px 0 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #ffffff;
    padding: 20px;
    list-style: none;
    border-radius: 5px;
}

.gift-listing-inner li {
    width: 24%;
}

.gift-listing-inner li img {
    border-radius: 5px;
}

.gift-listing-inner li p {
    margin: 15px 0 0;
    font-family: 'Lato', sans-serif;
    font-size: 24px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.21;
    letter-spacing: normal;
    text-align: center;
    color: #3a2414;
    text-transform: uppercase;
}
.mgift-listing-inner {
    margin: 0 0 0;
    margin-top: -20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #ffffff;
    padding: 20px;
    list-style: none;
    border-radius: 5px;
    flex-wrap: wrap;
}

.mgift-listing-inner li {
    width: 24%;
    margin-top: 40px;
    border-radius: 5px;
}

.mgift-listing-inner li img {
    border-radius: 5px;
    transition: border-radius ease-in-out 0.3s;
}
.mgift-listing-inner li:hover img {
    border-radius: 50%;
    transition: border-radius ease-in-out 0.3s;
}

.mgift-listing-inner li p {
    margin: 15px 0 0;
    font-family: 'Lato', sans-serif;
    font-size: 24px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.21;
    letter-spacing: normal;
    text-align: center;
    color: #3a2414;
    text-transform: uppercase;
}
.bg-heart-image {
    padding: 0px 2.2% 0px;
}
.bg-heart-image-inner {
    background: url(/bakingo/static/media/background-heart.09232431.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
    padding: 75px 2.2% 75px;
}
.confession_stories {
    padding: 100px 0 50px;
}
.confession_stories .confession_stories_inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.confession-image {
    position: relative;
    padding: 8px;
}
.confession-image .frame {
    position: absolute;
    top: -32px;
    left: -56px;
    right: 0;
    bottom: 0;
    margin: auto;
    min-width: 50%;
    min-height: 50%;
    z-index: 2;
}
.confession_stories .confession_stories_left {
    max-width: 25%;
    margin-left: 3%;
}
.confession_stories_left .swiper-button-prev, .confession_stories_left .swiper-button-next {
    display: none;
}
.confession_stories .confession_stories_right {
    max-width: 72%;
    padding: 10px 20px;
    padding-left: 7%;
    background: #e33d74;
    box-shadow: -5px 0px 20px 5px rgba(87,84,84,0.75) inset;
    -webkit-box-shadow: -5px 0px 20px 5px rgba(87,84,84,0.75) inset;
    -moz-box-shadow: -5px 0px 20px 5px rgba(87,84,84,0.75) inset;
    border-radius: 10px;
}

.confession_stories_right .banner-text {
    max-width: 90%;
    margin: 0 auto;
}
.confession_stories .confession_stories_right h4 {
    margin: 0 168px 1.2px 178px;
    font-family: Courgette;
    font-size: 50px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.26;
    letter-spacing: normal;
    text-align: left;
    color: #fff;
}
.confession_stories .confession_stories_right p {
    font-family: 'Rubik', sans-serif;
    font-size: 20px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.5;
    letter-spacing: normal;
    text-align: center;
    color: #fff;
} 
.confession_stories .confession_stories_right a {
    display: inline-block;
    padding: 5px 30px;
    background: #ffffff;
    color: #e33d74;
    text-transform: uppercase;
    border-radius: 25px;
    font-size: 15px;
    text-decoration: none;
    transition: 0.3s ease;
    border: 1px solid #ffffff;
}
.confession_stories .confession_stories_right a:hover {
    background: #e33d74;
    color: #ffffff;
    border: 1px solid #ffffff;
    transition: 0.3s ease;
}
.confession_stories .confession_stories_right .swiper-container {
    padding: 40px 0px;
}
.confession_stories .confession_stories_right .swiper-pagination {
    position: absolute;
    bottom: 10px;
}

.confession_stories_right .swiper-pagination-bullet {
    background: #ffffff;
    opacity: 0.5;
}
.confession_stories_right .swiper-pagination-bullet-active {
    opacity: 1;
}

.confession_stories_right .swiper-button-prev:after, 
.confession_stories_right .swiper-button-next:after {
    font-size: 25px;
    font-weight: 700;
    color: #ffffff;
}
